<template>
  <div>
    <div 
        class="selected-image" 
        :class="selectFilter" 
        :style="{backgroundImage: 'url('+image+')'}">
    </div>
    <div class="filter-container" v-dragscroll>
      <file-type 
        v-for="filter in filters" 
        :key="filter.name" 
        :filter="filter" :image="image">
      </file-type>
    </div>
  </div>
</template>

<script>
import fileType from "../fileType.vue";
export default {
  props: {
    selectFilter: String,
    image: String,
    filters: Array
  },
  components: {
    "file-type": fileType
  }
};
</script>