<template>
  <div>
    <div 
        class="selected-image" 
        :class="selectFilter" 
        :style="{backgroundImage: 'url('+image+')'}">
    </div>
    <div class="caption-container">
      <textarea 
        placeholder="Write a caption..." 
        :value="value" 
        @input="inputText">
    </textarea>
    </div>
  </div>
</template>

<script>
import eventBus from "../../event-Bus.js";
export default {
  props: {
    selectFilter: String,
    image: String,
    filters: Array,
    value: String
  },
  methods: {
    inputText(event) {
      eventBus.$emit("inputText", event.target.value);
    }
  }
};
</script>