<template>
    <div class="phone-body">
        <transition mode="out-in">
            <component 
                :is="step"
                :posts="posts"
                :selectFilter="selectFilter"
                :image="image"
                :filters="filters"
                :v-model='value'
            ></component>
        </transition>
        
    </div>
</template>

<script>
import step1 from './step/1.vue'
import step2 from './step/2.vue'
import step3 from './step/3.vue'
export default {
    props:{
        posts: Array,
        filters: Array,
        step: String,
        image: String,
        selectFilter: String,
        value: String
    },
    components:{
        "app-step-1": step1,
        "app-step-2": step2,
        "app-step-3": step3,
    },
    methods:{
        inputText(event){
            this.$emit("input",event.target.value);
        }
    },
}
</script>

<style lang="scss" src="../styles/phone-body.scss">

</style>

<style scoped>
    .v-enter{
        /* transform: translateX(375px); */
        opacity: 0;
    }
    .v-enter-active{
        transition: opacity 1s;
    }
    .v-enter-to{
        /* transform: translateX(0); */
        opacity: 1;
    }
    .v-leave{
        /* transform: translateX(0); */
        opacity: 1;
    }
    .v-leave-active{
        transition: opacity 1s;
    }
    .v-leave-to{
        /* transform: translateX(-187px); */
        opacity: 0; 
    }
</style>